body, html {
  height: 100%;
  margin: 20px;
}

.app-container {
  background-color: #fae;
  border-radius: 50px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: inline-block;
}

.top-controls {
  justify-content: space-around;
  align-items: center;
  gap: 25px;
  display: flex;
}

.bottom-controls {
  justify-content: space-around;
  align-items: center;
  gap: 25px;
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
}

.knob1-container, .knob2-container, .knob3-container, .knob4-container {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.metro-feedback-container, .playback-container {
  flex-direction: column;
  align-items: center;
  gap: 15px;
  display: flex;
}

.piano-roll {
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 300px;
  display: flex;
}

#pianoRollContainer {
  border: 1px solid #ccc;
  width: 600px;
  height: 300px;
}

.knob {
  background-color: #eee;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}

.knob-value, .recIndicator {
  color: #817f7f;
  margin-top: 5px;
  font-family: Roboto, monospace;
  font-size: 14px;
  font-weight: normal;
}

.control-btn img {
  width: 40px;
  height: 40px;
  display: inline-block;
  position: relative;
}

.light-base {
  vertical-align: middle;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: background-color 10ms;
  display: inline-block;
}

.light-off {
  background-color: #ff979736;
}

.light-on {
  background-color: #ff00bf;
}

.recording {
  color: red;
}

:root {
  --switch-scale: .5;
}

.switch {
  width: calc(60px * var(--switch-scale));
  height: calc(34px * var(--switch-scale));
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  height: calc(26px * var(--switch-scale));
  width: calc(26px * var(--switch-scale));
  left: calc(4px * var(--switch-scale));
  bottom: calc(4px * var(--switch-scale));
  background-color: #fff;
  transition: all .4s;
  position: absolute;
}

input:checked + .slider {
  background-color: #21d227;
}

input:focus + .slider {
  box-shadow: 0 0 1px #21d227;
}

input:checked + .slider:before {
  -webkit-transform: translateX(calc(26px * var(--switch-scale)));
  -ms-transform: translateX(calc(26px * var(--switch-scale)));
  transform: translateX(calc(26px * var(--switch-scale)));
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.arrayList-container, .reset-button {
  width: -moz-fit-content;
  width: fit-content;
  max-height: 150px;
  margin-bottom: 1px;
  display: none;
  overflow-y: scroll;
}

.midiOutputList {
  color: #817f7f;
  font-family: Roboto, monospace;
  font-size: 14px;
  font-weight: normal;
  display: none;
}

